import React from "react";

import "./style.scss";

export default function DisplayComponent(props) {
  const { tasks, selectedTasks, currentTask } = props;

  const getViewCountTasks = () => {
    return <div>{`Tasks: ${tasks.length}`}</div>;
  };

  const getViewCountTasksComplete = () => {
    return <div>{`Tasks complete: ${selectedTasks.length}`}</div>;
  };

  return (
    <div className="display margin-top10">
      {currentTask !== null ? "Edit status" : "Work status"}
      {getViewCountTasks()}
      {getViewCountTasksComplete()}
    </div>
  );
}
