import React, { useState, useEffect } from "react";
import "./style.scss";

export default function TaskComponent(props) {
  const { task } = props;
  const { id, text, status } = task;

  const {
    removeTask,
    selectTask,
    unselectTask,
    updateTask,
    currentEditTask
  } = props;
  const { currentTask } = props;

  const [isEdit, setEdit] = useState(false);
  const [isText, setText] = useState(text);

  useEffect(() => {
    setText(text);
    status ? selectTask(id) : unselectTask(id);
  }, [text]);

  const getStatusDiabled = () => {
    return currentTask !== null && currentTask !== id;
  };

  const getViewForm = () => {
    const handleEdit = () => {
      setEdit(!isEdit);
      currentEditTask({ id: id, text: text, status: status });
    };

    return (
      <div className="view">
        <label>
          <div className="text_and_input-menu">
            <input
              type={"checkbox"}
              defaultChecked={status}
              onChange={e => {
                e.currentTarget.checked ? selectTask(id) : unselectTask(id);
              }}
              disabled={getStatusDiabled()}
            />
            <div className={`task-text ${status && "text-strike"}`}>{text}</div>
          </div>
          <div className="button-menu">
            <input
              type={"button"}
              value={"edit"}
              onClick={() => {
                handleEdit();
              }}
              disabled={getStatusDiabled()}
            />
          </div>
        </label>
      </div>
    );
  };

  const getEditForm = () => {
    const handleSave = () => {
      setEdit(!isEdit);
      updateTask({ id: id, text: isText });
      currentEditTask(null);
    };
    const handleCancel = () => {
      setEdit(!isEdit);
      setText(text);
      currentEditTask(null);
    };
    const handleRemove = () => {
      setEdit(!isEdit);
      removeTask({ id: id });
      currentEditTask(null);
    };

    return (
      <div className="edit">
        <input
          type={"text"}
          value={isText}
          onChange={e => {
            setText(e.currentTarget.value);
          }}
        />
        <div className="button-menu">
          <input
            type={"button"}
            value={"save"}
            onClick={() => {
              handleSave();
            }}
            disabled={!isText}
          />
          <input
            type={"button"}
            value={"cancel"}
            onClick={() => {
              handleCancel();
            }}
          />
          <input
            type={"button"}
            value={"remove"}
            onClick={() => {
              handleRemove();
            }}
          />
        </div>
      </div>
    );
  };

  return (
    <div
      className={`task margin-top10 ${
        currentTask && currentTask.id === id ? "enabled" : ""
      }`}
    >
      {isEdit ? getEditForm() : getViewForm()}
    </div>
  );
}
