export const removeTask = (tasks, task) => {
  if (!task) {
    return tasks;
  }

  const { id } = task;
  let cloneTasks = [];

  [...tasks].map(el => {
    if (el.id !== id) {
      cloneTasks.push(el);
    }
  });

  return cloneTasks;
};

export const updateTask = (tasks, task) => {
  if (!task) {
    return tasks;
  }

  const { id } = task;
  let cloneTasks = [...tasks];

  cloneTasks.map((el, index) => {
    if (el.id === id) {
      cloneTasks[index] = { ...cloneTasks[index], ...task };
    }
  });

  return cloneTasks;
};

export const generateId = task => {
  if (!task) {
    return 0;
  }
  return task.id + 1;
};
