import React, { useState, useEffect } from "react";

import "./style.scss";

export default function MenuComponent(props) {
  const { tasks } = props;
  const { handleClick } = props;

  const { currentTask } = props;

  const [isText, setText] = useState("");

  useEffect(() => {
    setText("");
  }, [tasks]);

  return (
    <div className={`${currentTask !== null ? "disabled" : ""}`}>
      <div className="menu margin-top10">
        <input
          type={"text"}
          value={isText}
          onChange={e => {
            setText(e.currentTarget.value);
          }}
          placeholder={"new task..."}
          disabled={currentTask !== null}
        />
        <input
          type={"button"}
          value={"Add Task"}
          onClick={() => {
            handleClick(isText);
          }}
          disabled={currentTask !== null || !isText}
        />
      </div>
    </div>
  );
}
