import {
  ADD_TASK,
  REMOVE_TASK,
  SELECT_TASK,
  UNSELECT_TASK,
  UPDATE_TASK,
  CURRENT_EDIT_TASK
} from "../../actions";
import { removeTask, updateTask, generateId } from "../../../utils/tasksCRUD";

const initialContent = [
  {
    text: "Съездить в автосалон",
    status: false,
    id: 0
  },
  {
    text: "Приготовить пляжные шлепанцы",
    status: false,
    id: 1
  },
  {
    text: "Купить солнезащитные очки",
    status: true,
    id: 2
  },
  {
    text: "Заехать к Робу на пляж",
    status: false,
    id: 3
  },
  {
    text: "Порыбачить в заливе",
    status: true,
    id: 4
  },
  {
    text: "Починить каноэ",
    status: false,
    id: 5
  }
];

export const initialState = {
  tasks: [...initialContent],
  selectedTasks: [],
  currentTask: null
};

export function defaultReducers(state = initialState, action) {
  let cloneTasks = [...state.tasks];
  let cloneSelectedTasks = [...state.selectedTasks];
  let id = null;
  let text = null;
  let task = {};
  let incrementId = null;

  switch (action.type) {
    case ADD_TASK:
      incrementId = generateId(cloneTasks[cloneTasks.length - 1]);
      text = action.data;

      return {
        ...state,
        ...{
          tasks: [
            ...state.tasks,
            { text: text, status: false, id: incrementId }
          ]
        }
      };

    case REMOVE_TASK:
      task = action.data;

      cloneTasks = removeTask([...cloneTasks], task);
      cloneSelectedTasks = [...cloneSelectedTasks.filter(el => el !== task.id)];
      return {
        ...state,
        ...{
          tasks: [...cloneTasks],
          selectedTasks: [...cloneSelectedTasks]
        }
      };

    case SELECT_TASK:
      id = action.data;

      cloneTasks = updateTask([...cloneTasks], { id: id, status: true });
      return {
        ...state,
        ...{
          selectedTasks: [...cloneSelectedTasks, id],
          tasks: [...cloneTasks]
        }
      };

    case UNSELECT_TASK:
      id = action.data;

      cloneTasks = updateTask([...cloneTasks], { id: id, status: false });
      cloneSelectedTasks = [...state.selectedTasks.filter(el => el !== id)];
      return {
        ...state,
        ...{
          selectedTasks: [...cloneSelectedTasks],
          tasks: [...cloneTasks]
        }
      };

    case UPDATE_TASK:
      task = action.data;
      cloneTasks = updateTask([...cloneTasks], task);

      return {
        ...state,
        ...{
          tasks: [...cloneTasks]
        }
      };

    case CURRENT_EDIT_TASK:
      task = action.data;

      return {
        ...state,
        ...{
          currentTask: task
        }
      };

    default:
      return state;
  }
}
